    name: '',
    price: 1
  }, {
    name: '',
    price: 2
  }];

  @Provide() private foo = 'foo';

  private created() {
    this.isShowPage = true;
  }

  private toAbout() {
    this.$router.push({
      path: 'about',
      query: {
        a: '1'
      }
    });
